.modalBackground {
  position: fixed;
  z-index: 1;
  background-color: $COLOR_UI_BLACK;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

.modalContainer {
  box-shadow: 0 1px 4px $COLOR_UI_BLACK;
  border-radius: 6px;
  background-color: $COLOR_UI_WHITE;
  padding: 1rem;
  text-align: center;
  width: 30rem;
  z-index: 10;
  position: fixed;
  top: 20vh;
  left: calc(50% - 15rem);
}

.modalContainer .title {
  display: inline-block;
  text-align: center;
  color: $COLOR_UI_BLACK;
  margin-top: 10px;
  font-size: $FONT_SIZE_REGULAR;
  justify-content: center;
}

.img {
  height: 100px;
  width: 100px;
  margin: auto;
}
.closeBtn {
  display: flex;
  justify-content: flex-end;
}

.closeBtn button {
  background-color: transparent;
  border: none;
  font-size: $FONT_SIZE_REGULAR;
  cursor: pointer;
}

.modalContainer .footer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalContainer .footer button {
  width: 50%;
}
